import React from 'react';

import ImtuIcon from '../styles/img/svg/br-mtu-intl.svg';

import Layout from "../components/layout"
import Search from '../components/examples/imtu/Search';
import Transactions from '../components/examples/imtu/Transactions';
import Alert from '../components/Alert';
import SEO from "../components/seo"

export default class Imtu extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      alert: {
        type: '',
        message: '',
        show: false

      },
      historicalTransactions: []
    }
  }

  showAlert = (type, message) => {
    this.setState({
      alert: {
        type: type,
        message: message,
        show: true
      }
    });
  }

  onAlertClose = () => {
    this.setState({alert: {
        show: false
      }});
  }

  storeHistoricalTransactions = (transactions) => {
    this.setState({historicalTransactions: transactions});
  }

  placeOrder = () => {
    this.showAlert("info", "If you really want to place your order go ahead and do it at https://retailers.bossrevolution.com");
  }

  render(){
    return(
      <Layout>
        <SEO title="International Mobile Top-up" />
        <div className="container">
          <Alert
            message={this.state.alert.message}
            type={this.state.alert.type}
            show={this.state.alert.show}
            onAlertClose={this.onAlertClose}
          />
          <div className="island px-4 py-3">
            <h2>
              <ImtuIcon className="mr-2" />
              International Mobile Recharges
            </h2>
            <div className="row py-5 justify-content-center">
              <div className="col col-lg-4 col-md-8">
                <Search onError={this.showAlert} storeHistoricalTransactions={this.storeHistoricalTransactions} />
              </div>
            </div>
            <Transactions historicalTransactions={this.state.historicalTransactions} onError={this.showAlert} />
          </div>
          <div className="row mt-2">
            <div className="col-sm-6">
              <button className="btn btn-primary px-5 btn-block-sm mb-2" onClick={this.placeOrder}>
                Place order
              </button>
            </div>
            <div className="col-sm-6 text-right">
              <button className="btn btn-link btn-block-sm">
                Start over
              </button>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}