import React from 'react';
import { withPrefix } from 'gatsby';

import SearchIcon from '../../../styles/img/svg/br-search.svg';
import Countries from './Countries';
import ValidationHint from '../../ValidationHint';

const historicalTransactions = [
    {
        mode: 'view',
        date: '06/12/2020',
        sender: '54325234777',
        recipient: '50200000001',
        country: 'Guatemala',
        carrier: 'tigo',
        amount: 'tigo7',
        selected: false,
        isRinging: false
    },
    {
        mode: 'view',
        date: '05/10/2020',
        sender: '54325234523',
        recipient: '50200000001',
        country: 'Guatemala',
        carrier: 'tigo',
        amount: 'tigo7',
        selected: false,
        isRinging: false
    }
];

export default class Search extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            countryPopupVisible: false,
            searchHintVisible: false,
            number: '',
            selectedCountry: {
                RealCountryCode: "US",
                CountryName: "United States"
            }
        }
    }

    toggleCountryPopup = (event) => {
        this.setState({countryPopupVisible: !this.state.countryPopupVisible});
    }

    handleCountryClick = (country, event) => {
        this.setState({countryPopupVisible: false, selectedCountry: country, number: country.CountryDialingCode});
    }

    handleNumberChange = (event) => {
        this.setState({
            number: event.target.value,
            searchHintVisible: event.target.value === '' ? false : true
        });
    }

    handleSearchTransactions = () => {
        if(this.state.number === ''){
            this.props.onError('error', 'Please fill in FAKE number to search');
            return;
        }
        this.props.storeHistoricalTransactions(historicalTransactions);
    }

    render(){
        return (
            <div className="position-relative">
                <div className="input-group">
                    <div className="input-group-prepend">
                        <button className="btn btn-icon p-1"  onClick={this.toggleCountryPopup}>
                            <img height="30" width="30" className="rounded-circle" src={withPrefix("/") + 'flags/' + this.state.selectedCountry.RealCountryCode.toLowerCase() + '.svg'} alt={this.state.selectedCountry.CountryName} />
                        </button>
                    </div>
                    <input type="text" className="form-control h-auto" placeholder="Add + if international" value={this.state.number} onChange={this.handleNumberChange.bind(this)} />
                    <div className="input-group-append">
                        <button className="btn btn-brand p-1" onClick={this.handleSearchTransactions}>
                            <SearchIcon />
                        </button>
                    </div>
                </div>
                {
                    this.state.searchHintVisible ? <ValidationHint type="info" message="Press search button to see fake historical transactions"  /> : ''
                }                
                <div className={this.state.countryPopupVisible ? 'position-absolute floating-list w-100' : 'd-none'}>
                    <Countries handleCountryClick={this.handleCountryClick} />
                </div>
            </div>
        )
    }
}