import React from 'react';

import Header from './Header';
import Table from './Table';
import Thead from './Thead';
import Row from './Row';

const EMPTY_ROW = {
    mode: 'add',
    sender: '',
    recipient: '',
    country: '',
    carrier: '',
    amount: '',
    selected: false,
    isRinging: false
}

export default class Transaction extends React.Component{
    constructor(){
        super();

        this.state = {
            rows: [
                Object.assign({}, EMPTY_ROW)
            ]
        }
    }

    addTransaction = (event) => {
        this.setState(prevState => {
            let _rows = prevState.rows;

            for(let i = 0; i < _rows.length; i++){
                if(Object.values(_rows[i]).indexOf("") > -1){
                    this.props.onError('error', 'Please fill in all the fields');
                    return;
                }
            }

            _rows.push(Object.assign({}, EMPTY_ROW));
            return {
                rows: _rows
            }
        })
    }

    handleRowStateChange = (row, index) => {
        this.setState(prevState => {
            let _rows = prevState.rows;

            _rows[index] = Object.assign({}, row);
            return{
                rows: _rows
            }
        })
    }

    render(){
        return(
            <div className="transactions mx-n4">
                <Header />
                <Table
                    thead={<Thead />}
                    row={
                        this.state.rows.map((row, index) => {
                            return <Row row={row} key={'row-' + index} index={index} onRowChange={this.handleRowStateChange} />
                        })
                    } 
                    historyRow={
                        this.props.historicalTransactions.map((row, index) => {
                            return <Row row={row} key={'row-' + index} onRowChange={this.handleRowStateChange}/>
                        })
                    }
                />
                <div className="island-footer mt-2 px-4 py-3 mb-n3 rounded-bottom">
                    <button className="btn btn-outline-primary btn-block-sm" onMouseDown={this.addTransaction}>Add new transaction</button>
                </div>
            </div>
        )
    }
}