import React from 'react';
import { withPrefix } from 'gatsby';

const countries = [
    {CountryCode: "United States", CountryName: "United States", RealCountryCode: "US", CountryDialingCode: "1", AreaDialingCodes: null},
    {CountryCode: "ElSalvador", CountryName: "El Salvador", RealCountryCode: "SV", CountryDialingCode: "503", AreaDialingCodes: null},
    {CountryCode: "GH", CountryName: "Ghana", RealCountryCode: "GH", CountryDialingCode: "233", AreaDialingCodes: null},
    {CountryCode: "Guatemala", CountryName: "Guatemala", RealCountryCode: "GT", CountryDialingCode: "502", AreaDialingCodes: null},
    {CountryCode: "Haiti", CountryName: "Haiti", RealCountryCode: "HT", CountryDialingCode: "509", AreaDialingCodes: null},
    {CountryCode: "Honduras", CountryName: "Honduras", RealCountryCode: "HN", CountryDialingCode: "504", AreaDialingCodes: null},
    {CountryCode: "IN", CountryName: "India", RealCountryCode: "IN", CountryDialingCode: "91", AreaDialingCodes: null},
    {CountryCode: "MX", CountryName: "Mexico", RealCountryCode: "MX", CountryDialingCode: "52", AreaDialingCodes: null},
    {CountryCode: "Nicaragua", CountryName: "Nicaragua", RealCountryCode: "NI", CountryDialingCode: "505", AreaDialingCodes: null}
];

export default class Countries extends React.Component{
    render(){
        return(
            <ul className="list-group">
            {
                countries.map((country, index) => {
                    return (
                        <li key={index+'cntr'} className="list-group-item list-group-item-action px-2 py-1 border-0" onClick={this.props.handleCountryClick.bind(this, country)}>
                            <img className="country-flag mr-2 rounded-circle" src={withPrefix("/") + 'flags/' + country.RealCountryCode.toLowerCase() + '.svg'} alt={country.CountryName} />
                            <span>{country.CountryName}</span>
                            <span className="ml-auto">{country.CountryDialingCode}</span>
                        </li>
                        )
                })                        
            }
            </ul>
        )
    }
}