import React from 'react';

export default class Alert extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            type: '',
            message: '',
            visible: false,
            id: null,
            counting: false
        }
    }

    timer = null;

    componentDidUpdate = () => {
        if(!this.props.show){
            return false;
        }
        if(this.state.message !== this.props.message){
            this.timer = 'set';
            this.setState({
                message: this.props.message,
                visible: true,
                counting: true
            }, this.countDelay);
        }
        else {
            if(this.timer === null){
                this.timer = 'set';
                this.setState({visible: true, counting: true}, this.countDelay);
            }

            if(typeof(this.timer) === 'number' && this.state.counting === true){
                clearTimeout(this.timer);
                this.timer = null;
                this.setState({visible: false});
            }
        }
    }

    countDelay = () => {        
        this.timer = setTimeout(() => {
            this.setState({visible: false, counting: false}, () => {
                clearTimeout(this.timer);
                this.timer = null;
                this.props.onAlertClose();
            })
        }, 3000)
    }

    render(){
        return(
            <div className={"alert alert-" + this.props.type + " opacity-" + (this.state.visible ? 1 : 0)} style={{zIndex: (this.state.visible ? 10 : -1)}} >
                <div className="alert-header text-capitalize">
                    {this.props.type}
                    <button className="close" href="#close" />
                </div>
                <div className="alert-message">{this.props.message}</div>
            </div>
        )
    }
}