import React from 'react';

export default class Thead extends React.Component{
    render(){
        return(
            <thead>
                <tr>
                    <th>Date</th>
                    <th>Sender</th>
                    <th>Recipient</th>
                    <th>Country</th>
                    <th>Carrier</th>
                    <th>Amount</th>
                    <th width="130">Actions</th>
                </tr>
            </thead>
        )
    }
}