import React from 'react';

import Star from '../../../styles/img/svg/br-star.svg';
import Label from '../../../styles/img/svg/br-label.svg';
import BellIcon from '../../../styles/img/svg/br-bell.svg';

export default class Row extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            mode: props.row.mode,
            sender: props.row.sender,
            recipient: props.row.recipient,
            country: props.row.country,
            carrier: props.row.carrier,
            amount: props.row.amount,
            selected: props.row.selected,
            isRinging: props.row.isRinging,
            isRowValid: props.row.mode === 'add' ? false : true
        }
    }

    handleBellClick = () => {
        this.setState({isRinging: !this.state.isRinging});

        setTimeout(() => {
            this.setState({isRinging: false});
        }, 1500)
    }

    handleRowChange = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        }, () => {
            this.checkIsRowValid();
            this.props.onRowChange(this.state, this.props.index)
        })
    }

    handleCheckRow = (event) => {
        this.setState({
            selected: !this.state.selected
        }, () => {
            this.props.onRowChange(this.state, this.props.index)
        })
    }

    checkIsRowValid = () => {
        this.setState(prevState => {
            for(var prop in prevState){
                if(prevState[prop] === ''){
                    return;
                }
            }
            return {
                isRowValid: true
            }
        })
    }

    render(){
        let viewMode = this.state.mode === 'view';
        return(
            <tr>
                <td data-title="Date">{this.state.mode === 'view' ? this.props.row.date : 'Today'}</td>
                <td data-title="Sender">
                    <span className={viewMode ? '' : 'd-none'}>{this.state.sender}</span>
                    <div className={viewMode ? "d-none" : "input-group input-group-sm"}>
                        <input className="form-control" type="text" name="sender" onChange={this.handleRowChange.bind(this)} value={this.state.sender}></input>
                        <div className="input-group-append">
                            <button className="btn btn-icon py-0 px-1">
                                <Star />
                            </button>
                        </div>
                    </div>
                </td>
                <td data-title="Recipient">
                    <span className={viewMode ? '' : 'd-none'}>{this.state.recipient}</span>
                    <div className={viewMode ? "d-none" : "input-group input-group-sm"}>
                        <input className="form-control" type="text" name="recipient" onChange={this.handleRowChange.bind(this)} value={this.state.recipient}></input>
                        <div className="input-group-append">
                            <button className="btn btn-icon py-0 px-1">
                                <Label />
                            </button>
                        </div>
                    </div>
                </td>
                <td data-title="Country">
                    <span className={viewMode ? '' : 'd-none'}>{this.state.country}</span>
                    <select className={viewMode ? "d-none" : "form-control form-control-sm"} name="country" onChange={this.handleRowChange.bind(this)} value={this.state.country}>
                        <option value=''>Select a country</option>
                        <option value='Guatemala'>Guatemala</option>
                    </select>
                </td>
                <td data-title="Carrier">
                    <select className="form-control form-control-sm" name="carrier" onChange={this.handleRowChange.bind(this)} value={this.state.carrier}>
                        <option value=''>Select a carrier</option>
                        <option value='tigo'>Tigo</option>
                    </select>
                </td>
                <td data-title="Amount">
                    <select className="form-control form-control-sm" name="amount" onChange={this.handleRowChange.bind(this)} value={this.state.amount}>
                        <option value=''>Select an amount</option>
                        <option value='tigo7'>$7.00</option>
                    </select>
                </td>
                <td>
                    <span className="action d-flex">
                        <button className="btn btn-sm btn-icon btn-sm" onClick={this.handleBellClick}>
                            <BellIcon height="17" width="14" className={this.state.isRinging ? 'ringing' : ''} />
                        </button>
                        <span>
                            <input type="checkbox" className="check-box" id={"customCheck" + this.props.index} onChange={this.handleCheckRow.bind(this)} checked={this.state.selected} />
                            <label htmlFor={"customCheck" + this.props.index} className="mx-1" style={{fontSize: 22}} />
                        </span>
                    </span>
                </td>
            </tr>
        )
    }
}